<template>
  <div
    class="error-modal animate__animated animate__fadeInUp"
    v-if="ifError != 'null' || ifOffline != 'null'"
  >
    <!-- Modal Wraper  -->
    <div class="modal-wraper">
      <!-- Error Section  -->
      <section class="error">
        <p v-if="ifError != 'null'" class="error-message">A typo error please search again.</p>
        <p v-if="ifOffline != 'null'" class="error-message">The network connection has been lost.</p>
        <button class="exit-modal" @click="exitModal">exits</button>
      </section>
    </div>
  </div>
</template>

<script>
export default {
  props: ["ifError", "ifOffline"],
  data() {
    return {};
  },
  methods: {
    exitModal() {
      this.$emit("exit", "null");
    },
  },
  watch: {
    ifError: {
      immediate: true,
      handler(val, oldVal) {},
    },
  },
};
</script>

<style lang="scss">
.error-modal {
  position: relative;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
  .modal-wraper {
    position: absolute;
    z-index: 5;
    top: -30vh;
    right: 0;
    left: 0;
    .error {
      max-width: 300px;
      margin: auto;
      text-align: center;
      box-shadow: 1px 1px 10px #2222;
      background: #fff;
      border-radius: 3px;
      .error-message {
        font-size: 1rem;
        text-transform: capitalize;
        padding: 20px;
      }
      .exit-modal {
        margin-bottom: 20px;
        padding: 5px 20px;
        font-size: 1rem;
        text-transform: capitalize;
        border: 0px;
        background: #7f6061;
        color: #fff;
        border-radius: 3px;
        cursor: pointer;
      }
    }
  }
}
</style>
