<template>
  <div class="more-detail">
    <section class="details">
      <h1>more details</h1>
      <ul>
        <li v-if="weatherData.name">city : {{ weatherData.name }}</li>

        <li v-if="weatherData.sys.country">
          conutry : {{ weatherData.sys.country }}
        </li>

        <li v-if="weatherData.main.temp">
          temperature : {{ weatherData.main.temp }}&deg;
        </li>

        <li v-if="weatherData.main.feels_like">
          feels like : {{ weatherData.main.feels_like }}&deg;
        </li>

        <li v-if="weatherData.main.temp_max">
          max temp : {{ weatherData.main.temp_max }}&deg;
        </li>

        <li v-if="weatherData.main.temp_min">
          min temp : {{ weatherData.main.temp_min }}&deg;
        </li>

        <li v-if="weatherData.main.humidity">
          humidity : {{ weatherData.main.humidity }}
        </li>

        <li v-if="weatherData.main.pressure">
          pressure : {{ weatherData.main.pressure }}
        </li>

        <li v-if="weatherData.weather[0].description">
          weather : {{ weatherData.weather[0].description }}
        </li>

        <li v-if="weatherData.wind.speed">
          wind speed : {{ weatherData.wind.speed }}
        </li>

        <li v-if="weatherData.wind.deg">
          wind deg : {{ weatherData.wind.deg }}
        </li>

        <li v-if="weatherData.main.sea_level">
          sea level : {{ weatherData.main.sea_level }}
        </li>

        <li v-if="weatherData.main.grnd_level">
          ground level : {{ weatherData.main.grnd_level }}
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
import { watch, ref } from "vue";
import { useStore } from "vuex";
export default {
  setup() {
    const store = useStore();
    const weatherData = ref(store.getters.newWeather);
    watch(
      () => store.getters.newWeather,
      () => {
        weatherData.value = store.getters.newWeather;
      }
    );
    return { weatherData };
  },
};
</script>

<style lang="scss">
.more-detail {
  .details {
    overflow: hidden;
    h1 {
      font-size: 1.5rem;
      text-transform: capitalize;
      text-align: center;
      padding: 10px 0px;
      background: #252a48;
      color: #fff;
      font-weight: 400;
    }
    ul {
      list-style: none;
      li {
        padding: 10px;
        text-transform: capitalize;
        font-size: 1rem;
        box-shadow: 1px 1px 10px rgba(34, 34, 34, 0.233);
        margin:15px 10px;
        color: #222;
        font-weight: 300;
        border-radius: 3px;
        background: #fff;
      }
    }
  }
}
</style>
