<template>
  <div class="search">
    <!-- Search Input  -->
    <input
      type="text"
      v-model="searchInput"
      placeholder="Search by city name"
    />
    <!-- Search Button  -->
    <button class="search-btn" @click="fetchData">
      <i class="fa fa-search" aria-hidden="true"></i>
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchInput: "",
    };
  },
  methods: {
    // Fetch Data Function
    fetchData() {
      fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=${this.searchInput}&appid=${process.env.VUE_APP_API_KEY}&units=metric`
      )
        .then((response) => response.json())
        .then((data) => {
          console.log(data);
          this.searchInput = "";
          this.$emit("search", data);
        });
    },
  },
};
</script>

<style lang="scss">
.search {
  display: flex;
  input {
    width: 100%;
    border: 0px;
    background: #2222;
    text-indent: 10px;
    padding: 10px 0px;
    font-size: 1rem;
  }
  .search-btn {
    padding: 10px 30px;
    border: 0px;
    background: #7f6061;
    i {
      font-size: 1.2rem;
      color: #fff;
      pointer-events: none;
    }
  }
}
</style>
