<template>
  <div class="showcase">
    <!-- Prev Button -->
    <div class="prev-btn">
      <button class="btn">
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </button>
    </div>
    <!-- Main Content -->
    <div
      v-if="weather"
      :class="{
        'current-weather': true,
        'animate__animated animate__fadeInUp': weather,
      }"
    >
      <section class="location">
        <div class="description">
          <img
            :src="
              `https://openweathermap.org/img/w/${weather.weather[0].icon}.png`
            "
            alt="icon"
          />
          <p>{{ weather.weather[0].description }}</p>
        </div>
        <h1 class="city-name">{{ weather.name }}</h1>
        <h1 class="country-name">country {{ weather.sys.country }}</h1>
      </section>
      <section class="weather">
        <h1 class="temperature">{{ weather.main.temp }}&deg;</h1>
        <div class="current-time">
          <p>{{ currentTime }}</p>
        </div>
      </section>
    </div>
    <!-- Next Button -->
    <div class="next-btn">
      <button class="btn">
        <i class="fa fa-chevron-left" aria-hidden="true"></i>
      </button>
    </div>
  </div>
</template>

<script>
import { onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
export default {
  props: ["data", "searchData"],
  setup(props) {
    const weatherData = props.data.list;
    const weather = ref(weatherData[0]);
    const store = useStore();
    const currentTime = ref(Date());
    store.commit("update", weather);
    //Next and Prev Function
    onMounted(() => {
      const btns = document.querySelectorAll(".btn");
      const count = ref(0);
      btns.forEach((item) => {
        item.addEventListener("click", (e) => {
          const x = document.querySelector(".current-weather");
          if (x.classList.contains("animate__fadeInUp")) {
            x.classList.remove("animate__fadeInUp");
            x.classList.add("animate__fadeInDown");
          } else {
            x.classList.remove("animate__fadeInDown");
            x.classList.add("animate__fadeInUp");
          }

          if (e.target.parentElement.classList.contains("prev-btn")) {
            count.value++;
            if (count.value >= Object.values(weatherData).length)
              count.value = 0;
            console.log(weatherData[count.value]);
            weather.value = weatherData[count.value];
          } else {
            count.value--;
            if (count.value < 0)
              count.value = Object.values(weatherData).length - 1;
            console.log(weatherData[count.value]);
            weather.value = weatherData[count.value];
          }
        });
      });
    });
    // Watching Search Data
    watch(
      () => props.searchData,
      () => {
        console.log(props.searchData);
        weather.value = props.searchData;
      }
    );
    // updating Time 
    const getCurrentTime = () => {
      currentTime.value = new Date().toUTCString().replace("GMT", "");
      setTimeout(getCurrentTime, 500);
    };
    getCurrentTime();
    return { weather, currentTime, getCurrentTime };
  },
};
</script>

<style lang="scss">
.showcase {
  display: grid;
  grid-template-columns: 0.2fr 1fr 0.2fr;
  grid-template-rows: auto;
  background: url("../assets/images/bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
  color: #fff;
  min-height: 50vh;
  .current-weather {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    align-self: center;
    justify-self: center;
    .location {
      .description {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 60px;
        }
        p {
          text-transform: capitalize;
          font-size: 1.2rem;
          padding: 0px 5px;
        }
      }
      h1 {
        text-transform: capitalize;
        text-align: center;
        color: #fff;
        font-size: 1.5rem;
      }
      .city-name {
        font-weight: 600;
        padding: 10px 0px;
      }
      .country-name {
        font-weight: 300;
      }
    }
    .weather {
      padding-bottom: 40px;
      .temperature {
        font-size: 4rem;
        text-align: center;
        font-weight: 400;
      }
      .current-time {
        p {
          font-size: 1rem;
          text-align: center;
          font-weight: 300;
        }
      }
    }
  }
  button {
    background: transparent;
    color: #fff;
    border: 0px;
    padding: 10px;
    cursor: pointer;
    i {
      font-size: 1.2rem;
      pointer-events: none;
    }
  }
  .next-btn {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: center;
    justify-self: center;
  }
  .prev-btn {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    align-self: center;
    justify-self: center;
  }
}
@media only screen and(max-width:500px) {
  .showcase {
    .current-weather {
      .weather {
        .temperature {
          font-size: 3.5rem;
        }
      }
    }
  }
}
@media only screen and(max-width:370px) {
  .showcase {
    .current-weather {
      .location {
        .description {
          p {
            font-size: 0.9rem;
          }
        }
        h1 {
          font-size: 1.2rem;
        }
      }
      .weather {
        padding: 0px;
        .temperature {
          font-size: 3rem;
        }
      }
    }
  }
}
@media only screen and(min-width:1200px) {
  .showcase {
    .current-weather {
      .location {
        .description {
          p {
            font-size: 1.5rem;
          }
        }
        h1 {
          font-size: 2.5rem;
        }
      }
      .weather {
        .temperature {
          font-size: 4.5rem;
        }
        .current-time {
          p {
            font-size: 1.5rem;
          }
        }
      }
    }
    button {
      i {
        font-size: 2.5rem;
      }
    }
  }
}
</style>
