<template>
  <div class="home">
    <!-- Show casee Section  -->
    <section class="showcase-section">
      <ShowCase v-if="data" :data="data" :searchData="searchData" />
    </section>
    <!-- Search Section  -->
    <section class="search-section">
      <Search v-if="data" @search="search" />
    </section>
    <!-- Error Modal popup  -->
    <section class="modal-section">
      <ErrorModal :ifError="ifError" :ifOffline="ifOffline" @exit="exitModal" />
      
    </section>
    <!-- More Detail  section-->
    <section class="more-detail-section">
      <MoreDetail v-if="data" :data="data" />
    </section>
    <!-- Footer section  -->
    <section class="footer-section">
      <Footer v-if="data" />
      <LoadingAnimation v-if="!data"/>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
// Components
import ShowCase from "../components/ShowCase";
import Search from "../components/Search";
import ErrorModal from "../components/ErrorModal";
import MoreDetail from "../components/MoreDetail";
import Footer from "../components/Footer";
import LoadingAnimation from "../components/LoadingAnimation";
// Composable
import getNewData from "../composable/getData";
import Localbase from "localbase";
let db = new Localbase("db");
import { onBeforeMount, ref, watch } from "vue";
export default {
  name: "Home",
  components: {
    ShowCase,
    Search,
    ErrorModal,
    MoreDetail,
    Footer,
    LoadingAnimation
  },
  setup() {
    const data = ref(null);
    const searchData = ref("null");
    const ifError = ref("null");
    const ifOffline = ref("null");
    // Get Current Data
    onBeforeMount(async () => {
      db.collection("weatherData")
        .get()
        .then((users) => {
          console.log(users);
          if (users.length > 0) {
            data.value = users[0].data;
          }
        });
      getNewData(data);
    });
    // watching data and Storing Data to Localbase
    watch(data, () => {
      const localData = data;
      db.collection("weatherData").delete();
      db.collection("weatherData").add({
        data: localData._rawValue,
      });
    });
    // Creat Prop Data Funtion
    function search(value) {
      if (value.message) {
        ifError.value = 404;
        return;
      } else {
        console.log(value);
        searchData.value = value;
      }
    }
    // Exit MoDal
    const exitModal = (value) => {
      ifError.value = value;
      ifOffline.value = value;
    };
    // Update Data
    const updateData = () => {
      setInterval(() => {
        if (navigator.onLine === true) {
          getNewData(data);
        } else {
          return;
        }
      }, 60000);
    };
    // Getting Data when newwork is onLine 
    window.addEventListener("online", (event) => {
      getNewData(data);
    });
    // Creating popup when offline 
    window.addEventListener("offline", (event) => {
      ifOffline.value = "Offline";
    });
    updateData();
    return { data, search, searchData, ifError, exitModal, ifOffline };
  },
};
</script>
<style lang="scss">
.home {
  max-width: 100%;
  margin: auto;
}
</style>
