import { createStore } from "vuex";

export default createStore({
  state: {
    weatherData: null,
  },
  mutations: {
    update(state, payload) {
      state.weatherData = payload;
    },
  },
  actions: {},
  getters: {
    newWeather(state) {
      return state.weatherData;
    },
  },
  modules: {},
});
